.header {
  display: flex;
  flex-direction: column;

  margin-bottom: 100px;

  @include grid;

  &__topbar {
    grid-column: 1 / -1;
  }

  &__greeting {
    grid-column: 1 / -1;
  }

  @include on-tablet {
    margin-bottom: 130px;
  }

  @include on-desktop {
    padding-left: 50px;
    padding-right: 50px;

    margin-bottom: 200px;
  }

  &__allProd {
    margin-bottom: 30px;
  }
}
